body {
  background-color: rgb(19,19,23);
}

.container-theme {
  border-radius: 10px;
  background-color: rgba(34, 30, 30, 0.83);
  @apply my-4 py-8 md:my-12 md:p-12;
  
}
.counter-cell{
  background-color: rgba(34, 30, 30, 0.83);
  border-radius: 10px;
}

.header-theme {
  color: rgb(255, 255, 255);
  @apply text-left;
}

.subhead-theme {
  /* color: rgb(212, 212, 212); */
  color:#0891b2;
  /* @apply text-left; */
}

.faq-theme{
  color: rgb(212, 212, 212);
  @apply text-left;
}

.content-theme {
  color: rgb(177, 177, 177);
}
.header-font{
  color:aliceblue
}
.nav-theme{
  background-color: rgba(34, 30, 30, 0.83);                                              
  @apply md:opacity-90;

}
@media only screen and (max-width: 768px) {
  .nav-theme{
    background-color: rgb(38, 38, 46); 
  }                                             
}

.activeColor{
  background-color: rgba(11, 11, 11, 0.5);
}

.xl3{
  font-family: 'Poppins', sans-serif;
  font-weight: 351 !important;
