.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: rgb(38, 38, 46);
  color: rgb(195, 199, 204);
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  
  left: 105%;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}